.teams {
  @include respond(big-desktop) {
    height: 70vh;
  }
  background: url(../img/background.jpg);
  // linear-gradient(
  //     to bottom,
  //     transparent 25%,
  //     transparent 50%,
  //     transparent 75%,
  //     #e74d3cb8 85%
  //   ),
  height: 55vh;
  position: relative;
  overflow: hidden;
  padding-top: 3rem;
  &-wave {
    height: 67%;
    width: 100%;
  }
  &-wave__main {
    fill: #e74d3cb8;
    opacity: 0.5;
    transform: translateY(-5.5rem) scale(1.6);
    height: 100%;
  }
  &__img {
    width: 5.5rem;
    height: 5.5rem;
    transition: all 0.6s ease;
    &::after {
      width: 3rem;
      background-color: $color-grey-light-1;
      height: 0.35rem;
      top: 100%;
      left: 13%;
      transform: rotate(90deg);
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
    }
    &:hover {
      transform: scale(1.2);
    }
    &:hover .teams__intro {
      opacity: 1;
      visibility: visible;
      pointer-events: all;
    }
    &--3:hover .teams__intro {
      transform: translate(-3rem, -13rem);
      opacity: 1;
      visibility: visible;
      pointer-events: all;
    }
    &--4:hover .teams__intro {
      transform: translate(-3rem, -13rem);
      opacity: 1;
      visibility: visible;
      pointer-events: all;
    }
    &:hover::after {
      display: none;
      top: 149%;
      opacity: 1;
      visibility: visible;
      pointer-events: all;
    }
    img {
      outline: 3px solid #e74c3c;
      border-radius: 50%;
      width: 100%;
      height: 100%;

      &:hover {
        background-image: linear-gradient(#e74d3c73, #e74d3c7e);
      }
    }
    &--1 {
      position: absolute;
      top: 64%;
      left: 16%;
      @include respond(big-desktop) {
        top: 61%;
        left: 12%;
      }
    }
    &--2 {
      position: absolute;
      top: 67%;
      left: 35%;
      @include respond(big-desktop) {
        top: 67%;
        left: 31%;
      }
    }
    &--3 {
      position: absolute;
      top: 77%;
      left: 50%;
      @include respond(big-desktop) {
        top: 75%;
        left: 50%;
      }
    }
    &--4 {
      position: absolute;
      top: 75%;
      left: 68%;
      @include respond(big-desktop) {
        top: 52%;
        left: 71%;
      }
    }
    &--5 {
      position: absolute;
      top: 56%;
      left: 86%;
      @include respond(big-desktop) {
        top: 26%;
        left: 91%;
      }
    }
  }
  &__intro {
    padding: 0.5rem 1rem;
    border: 2px solid orangered;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    span {
      display: block;
      font-size: 1rem;

      color: #fff;
    }
    color: orangered;
    line-height: 1.6;
    font-size: 1.6rem;
    width: 10rem;
    display: block;
    transform: translate(-2rem, 2rem);
    transition: all 0.6s ease;
    text-align: center;
  }
}
