.blog-grid {
  margin-top: 10rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  height: 70vh;
  &__item {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
.grid__text {
  background-color: $color-primary-light;
  padding: 3rem;
  color: $color-grey-light-2 !important;
  & > * {
    text-align: left !important;
  }
  p {
    font-size: 0.9rem;
    line-height: 2;
  }
  & > *::after {
    background-color: $color-grey-light-2;
    width: 5rem;
    height: 0.35rem;
    top: 126%;
    left: 0%;
  }
  .button--sec{
    border: none;
    box-shadow: none;
  
  }
}
