.clients {
  margin-top: 15rem;
  width: 57%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 2rem;
  &__logo {
    padding: 1rem;
    @include respond(big-desktop) {
      padding: 2rem 3rem;
    }
    box-shadow: 0.5px 0.5px 5px #00000065;
    background-color: $color-grey-light-1;
    &:nth-child(7) {
      background-color: $color-primary-dark;
    }
    img {
      opacity: 0.35;
      width: 11rem;
      height: 5rem;
      @include respond(big-desktop) {
        width: 12rem;
        height: 6rem;
      }
    }
  }
}
