.service {
  //   background: $color-grey-dark-4 url(../img/services.jpg) 50% right no-repeat;
  height: 60vh;
  background-color: $color-grey-dark-4;
  background-image: url(../img/services.jpg);
  background-size: 50%;
  background-position: right;
  background-repeat: no-repeat;
  @include respond(big-desktop) {
    height: 72vh;
  }
  &__content {
    max-width: 50%;
    padding: 10rem 3rem;
    @include flexColCenter();
    align-items: end;
  }
  &__heading {
    color: $color-grey-light-1;
    font-size: 2rem;
    margin-bottom: 3rem;
    &::after {
      border-radius: 9%; 
      left: 97% !important;
    }
  }
  &__para {
    width: 80%;
    text-align: right;
    &:not(:last-child) {
      margin: 2rem 0;
    }
    h2 {
        margin-bottom: 1rem;
      color: $color-primary-dark;
      font-size: 1.6rem;
    }
    p {
      color: $color-grey-dark-3;
      font-size: 1.2rem;
      line-height: 2;
    }
  }
}
