.pricing {
  @include flexRowCenter();
  gap: 2rem;
  &__list {
    @include flexColCenter();
    gap: 2rem;
    justify-content: center;
    text-align: center;
  }
  @include respond(big-desktop) {
    margin-top: 8rem;
  }
}
.price-box {
  background-color: $color-grey-light-2;
  flex: 0 1 25%;

  height: 50rem;
  &__header {
    padding: 2rem;
    background: #8d8989e6;
    height: 35%;
  }
  &__price {
    height: 12rem;
    width: 12rem;
    border-radius: 50%;
    background-color: $color-grey-light-1;
    border: 5px solid #b1acacb8;
    position: relative;
    top: 79px;
    left: 29%;
    @include respond(big-desktop) {
      left: 33%;
    }
    @include flexColCenter();
    justify-content: center;
    p {
      color: $color-grey-dark-3;
      font-size: 2.8rem;
      text-align: center;
      font-weight: 700;
      span {
        display: block;
        text-align: center;
        font-size: 1.2rem;
        font-weight: 300;
      }
    }
  }
  button {
    background: #8d8989e6;
    margin: 4rem auto;
    display: block;
  }
  &__list {
    margin-top: 10rem;
  }
}
.price-box-main {
  flex: 0 1 25%;

  height: 50rem;
  &__header {
    padding: 2rem;
    background: #c10b0bdb;
    height: 35%;
  }
  &__price {
    height: 12rem;
    width: 12rem;
    border-radius: 50%;
    background-color: $color-grey-light-1;
    border: 5px solid $color-primary-light-2;
    position: relative;
    top: 79px;
    left: 29%;
    @include respond(big-desktop) {
      left: 33%;
    }
    @include flexColCenter();
    justify-content: center;
    p {
      color: $color-primary-dark;
      font-size: 2.8rem;
      text-align: center;
      font-weight: 700;
      span {
        display: block;
        text-align: center;
        font-size: 1.2rem;
        font-weight: 300;
      }
    }
  }
  button {
    background-color: #c10b0bdb;
    margin: 4rem auto;
    display: block;
  }
  &__list {
    margin-top: 10rem;
  }
}
