.case {
  position: relative;
  @include flexRowCenter();
  gap: 0;

  &__content {
    @include flexColCenter();

    position: relative;
    left: 7rem;
    z-index: 100;
    box-shadow: 2px 5px 11px #0000007e;
    gap: 3rem;
    width: 35%;
    height: 41rem;

    padding: 3rem;

    background-color: $color-grey-light-1;
    @include respond(big-desktop) {
      width: 32%;
      height: 48rem;
      padding: 7rem;
    }
  }
  &__txt {
    @include flexColCenter();
    gap: 2rem;
    justify-content: center;
    margin-top: 3rem;
    .heading-tertiary + p {
      @extend .heading-secondary__sub;
      margin: 2rem 0;
      text-align: center;
      font-size: 1.15rem;
      text-transform: capitalize;
      letter-spacing: 1px;
      line-height: 1.45;
    }

    & > h3::after {
      top: 200%;
      left: 40%;
    }
  }
  &__logo {
    display: inline-block;
    background-color: $color-primary-dark;
    padding: 3rem;

    transform: rotate(45deg);
    & > * {
      @include absCenter();
      top: 30%;
      left: 31%;
      stroke: $color-grey-light-1 !important;
      height: 2.5rem;
      width: 2.5rem;
      transform: rotate(-45deg);
    }
  }
  &__image {
    max-width: 34%;

    & > * {
      max-height: 100%;
      max-width: 100%;
    }
  }
}
