.history {
  height: 55vh;
  width: 70%;
  gap: 0rem !important;
  @include respond(big-desktop) {
    gap: 2rem !important;
  }
  @include flexRowCenter();
  &__image {
    max-width: 50%;
    & > * {
      max-width: 88%;
      @include respond(big-desktop) {
        max-width: 101%;
      }
    }
  }
  &__content {
    padding: 3rem;

    max-width: 50%;
    p {
      @include respond(big-desktop) {
        font-size: 1.4rem !important;
      }
      font-size: 1.2rem;
      line-height: 1.5;
      &:not(:last-child) {
        margin-bottom: 2rem;
      }
    }
  }
}
